@import "utils";

.logo {
  background-image: url("../images/logo/dia-logo.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 50px;
  height: 26px;

  &__link {
    display: block;
    width: 100%;
    height: 100%;

    &:hover {
      cursor: pointer;
    }
  }
}
